import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'

import store from '../store'
import AppLayout from '../components/admin/AppLayout'
import Dashboard from '../components/dashboard/Dashboard'

Vue.use(Router)

var demoAuthCredentials = {
  email: 'design.lmcorp@gmail.com',
  password: 'useruserhelp',
}

const routes = [
  {
    path: '*',
    redirect: { name: 'dashboard' },
  },
  {
    path: '/404',
    component: () =>
      import('@/components/pages/404-pages/VaPageNotFoundSimple.vue'),
  },
  {
    name: 'Admin',
    path: '/admin',
    redirect: '/admin/dashboard',
    component: AppLayout,
    children: [
      {
        name: 'dashboard',
        path: 'dashboard',
        component: Dashboard,
        default: true,
      },
      {
        name: 'deposits',
        path: 'deposits',
        component: () => import('../components/deposits/Deposits.vue'),
      },
      {
        name: 'settings',
        path: 'settings',
        component: () => import('../components/settings/Settings.vue'),
      },
      {
        name: 'domains',
        path: 'domains',
        component: () => import('../components/domains/Domains.vue'),
      },
    ],
  },
]

const router = new Router({
  mode: 'history',
  routes,
})

router.beforeResolve((to, from, next) => {
  const devAuthToken = store.state.auth.token
  if (devAuthToken) { // DEV
    console.log('ROUTER: DEV MODE')
    axios
      .get(store.state.app.config.base + '/api/admin/auth', {
        headers: {
          Authorization: devAuthToken
        },
      })
      .then(({ data: user }) => {
        store.commit('AUTH', {
          user, token: devAuthToken
        })
        // window.location.reload(false)
        localStorage.setItem('auth-token', devAuthToken)
        next()
      })
  } else if (!store.state.user) { // PROD
    console.log('ROUTER: PROD MODE')
    axios
      .get(store.state.app.config.base + '/api/admin/auth', {
        headers: {
          Authorization: localStorage.getItem('auth-token'),
        },
      })
      .then(response => {
        store.commit('AUTH', { user: response.data, token: localStorage.getItem('auth-token') })
        next()
      })
      .catch(err => {
        sessionStorage.removeItem('auth-token')
      })
  } else {
    next()
  }
})

export default router
