<template>
  <div
    class="transaction-item"
    :style="{
      'grid-template-columns': isDesktop
        ? '1fr 0.5fr 0.8fr 0.7fr 0.1fr'
        : '1fr 1fr 1fr 0.5fr',
    }"
  >
    <transaction-prop
      align="start"
      style="opacity: .7"
      :description="'out=>' + value.date"
      v-if="isDesktop"
    />
    <transaction-prop
      flat
      @click="copyAddress(value.address)"
      :description="'btn=>' + cropAddress(value.address)"
    />
    <transaction-prop :description="'out=>' + value.amount" />
    <transaction-prop :description="'sts=>' + value.status" />
    <transaction-prop
      align="start"
      flat
      color="danger"
      @click="$emit('delete', value.id)"
      description="btn(trash)=>_"
    />
  </div>
</template>

<script>
import TransactionProp from './TransactionProp.vue'

export default {
  components: {
    TransactionProp,
  },
  props: {
    value: {
      type: Object,
    },
  },
  data() {
    return {}
  },
  computed: {
    isDesktop() {
      return this.$store.getters.isDesktop
    },
  },
  methods: {
    copyAddress(s) {
      this.$copyText(s)
      this.showToast('Copied!', {
        icon: 'fa-copy',
        position: 'bottom-center',
        duration: 4000,
        type: 'info',
      })
    },
    cropAddress(s) {
      return `${s.match(/^.{6}/)[0]}...${s.match(/.{4}$/)}`
    },
  },
  created() {},
}
</script>

<style lang="scss" scoped>
.transaction-item {
  width: calc(100% - 1rem);
  margin: 0.1rem 0.5rem;
  padding: 0 0.8rem;

  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 0.5fr 0.5fr 0.5fr 0.1fr;

  background-color: #00000033;
  border-top: 2px #ffffff22 solid;
  justify-content: space-between;
}
</style>
