<template>
  <va-inner-loading :loading="loading || updating">
    <div class="tab" :style="computedStyle">
      <div class="tab__create flex">
        <va-accordion>
          <va-collapse
            :updateHeight="
              !!(selectedType || currentDateToggle || collapseUpdateToggle)
            "
          >
            <span slot="header">
              {{ $t('dashboard.profile.tabs.transactions.create') }}
            </span>
            <div slot="body">
              <va-select
                :options="['BTC', 'LTC', 'ETH', 'USDC']"
                v-model="selectedCurrency"
                class="ma-0"
                :label="$t('dashboard.profile.tabs.transactions.currency')"
                no-clear
              />
              <va-input
                class="mb-3"
                :value="wallets[selectedCurrency]"
                :label="$t('dashboard.profile.tabs.transactions.balance')"
                disabled
              ></va-input>
              <va-select
                class="ma-0"
                :options="types"
                v-model="selectedType"
                :label="$t('dashboard.profile.tabs.transactions.action')"
                no-clear
              />
              <va-input
                class="mb-3"
                value=""
                v-model="addressToWithdraw"
                :label="$t('dashboard.profile.tabs.transactions.address')"
                v-if="selectedType === 'Withdrawal'"
              ></va-input>
              <va-select
                class="ma-0"
                v-if="selectedType === 'Transfer'"
                :options="['Sent', 'Received']"
                v-model="selectedDirection"
                :label="$t('dashboard.profile.tabs.transactions.direction')"
                no-clear
              />

              <va-input
                class="mb-3"
                label="Amount"
                v-model="coinAmount"
              ></va-input>
              <va-checkbox
                class="mb-3"
                v-model="currentDateToggle"
                :label="
                  currentDateToggle
                    ? $t('dashboard.profile.tabs.transactions.currentTime')
                    : $t('dashboard.profile.tabs.transactions.customTime')
                "
              />
              <div class="mb-3" v-if="!currentDateToggle">
                <input type="date" v-model="customDate" />
                <input type="time" v-model="customTime" />
              </div>
              <va-button
                class="mt-2 justify--center"
                small
                color="info"
                @click="createTransaction"
              >
                {{ $t('dashboard.profile.tabs.transactions.submit') }}
              </va-button>
            </div>
          </va-collapse>
        </va-accordion>

        <div class="tab__status-editing" v-if="editingTransaction">
          <h2 style="margin-bottom: .5rem; margin-left: 1rem">
            Withdrawal status editing
          </h2>
          <va-input
            class="ma-2"
            :value="newWithdrawalStatus"
            v-model="newWithdrawalStatus"
            placeholder="New status"
          >
            <va-icon name="fa fa-edit" slot="prepend" />
          </va-input>
          <va-button-toggle
            outline
            small
            v-model="currentStatusType"
            :options="statusTypesOptions"
            style="margin: .9rem 0"
            :color="['success', 'warning', 'danger'][this.currentStatusType]"
          />
          <va-button
            style="padding: 0.3rem 0.5rem"
            outline
            color="info"
            @click="saveStatus"
          >
            Save status
          </va-button>
        </div>

        <div class="tab__transactions-list-header">
          <va-button
            style="padding: 0.3rem 0.5rem"
            small
            outline
            color="info"
            @click="refreshTransactions"
          >
            <va-icon name="fa fa-refresh" />
          </va-button>
          <h2>{{ selectedType }}s list</h2>
          <va-button-toggle
            outline
            small
            v-model="selectedType"
            :options="typesOptions"
          />
        </div>

        <transfer
          v-for="t of transfers"
          :value="t"
          :key="t.id"
          @delete="deleteTransaction"
        />
        <deposit
          v-for="t of deposits"
          :value="t"
          :key="t.id"
          @delete="deleteTransaction"
        />
        <withdrawal
          v-for="t of withdrawals"
          :value="t"
          :key="t.id"
          @edit="editStatus"
          @verify="verifyWithdrawal"
          @delete="deleteTransaction"
        />

        <div class="no-data" v-if="noSelectedTransactions">
          No {{ selectedType }}s
        </div>

        <div class="tab__pagination_wrap" v-if="pages > 1">
          <va-pagination
            v-model="activePage"
            :pages="pages"
            :visiblePages="5"
            small
          ></va-pagination>
        </div>
      </div>
    </div>
  </va-inner-loading>
</template>

<script>
import * as Transactions from '@/api/Transactions'

import Transfer from './Transfer'
import Withdrawal from './Withdrawal'
import Deposit from './Deposit'

import _ from 'lodash'
import moment from 'moment'

export default {
  name: 'profile-transactions-tab',
  props: ['loading', 'user'],
  components: {
    Transfer,
    Withdrawal,
    Deposit,
  },
  data: () => ({
    selectedCurrency: 'BTC',
    selectedType: 'Transfer',
    types: ['Transfer', 'Deposit', 'Withdrawal'],
    typesOptions: [
      { label: 'Transfer', value: 'Transfer' },
      { label: 'Deposit', value: 'Deposit' },
      { label: 'Withdrawal', value: 'Withdrawal' },
    ],
    statusTypesOptions: [
      { label: 'Success', value: 0 },
      { label: 'Processing', value: 1 },
      { label: 'Error', value: 2 },
    ],
    newWithdrawalStatus: '',
    currentStatusType: 0,
    editingTransaction: null,

    selectedDirection: 'Received',
    newWithdrawalStatus: '',
    currentDateToggle: true,
    addressToWithdraw: '',
    customDate: moment().format('YYYY-MM-DD'),
    customTime: moment().format('HH:mm'),
    coinAmount: 0.01,
    updating: false,
    activePage: 1,
    statusTypes: ['error', 'success', 'processing'],
    collapseUpdateToggle: false,
  }),
  watch: {
    selectedType() {
      this.editingTransaction = null
    },
    newWithdrawalStatus(v) {
      if (this.editingTransaction) this.editingTransaction.status = v
    },
  },
  computed: {
    customDatetime() {
      return this.customDate + ' ' + this.customTime
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.updating || this.loading ? 0.2 : 1,
      }
    },
    wallets() {
      return {
        BTC: this.user.wallets.bitcoin.balance,
        LTC: this.user.wallets.litecoin.balance,
        ETH: this.user.wallets.ethereum.balance,
        USDC: this.user.wallets['usd coin'].balance,
      }
    },
    pages() {
      return _.chunk(
        this.user.transactions.filter(t => t.name === this.selectedType),
        6
      ).length
    },
    transactions() {
      let asset = this.selectedType

      let result = _.chunk(
        this.user.transactions
          .sort((ta, tb) => (ta.at > tb.at ? -1 : ta.at < tb.at ? 1 : 0))
          .filter(t => t.name === asset),
        6
      )

      result = result.length
        ? result[this.activePage - 1].map((t, i) => {
            let locale = this.$time.UTCToLocale(t.at)
            let formated = this.$time.format(locale)

            if (t.name === 'Transfer') {
              return {
                name: t.name,
                id: t.id,
                date: formated,
                type: t.type,
                fake: t.fake ? 'fake' : 'real',
                status: t.status,
                currency: t.currency,
                amount:
                  t.amount +
                  ' ' +
                  {
                    Ethereum: 'ETH',
                    Bitcoin: 'BTC',
                    Litecoin: 'LTC',
                    'Usd coin': 'USDC',
                  }[t.currency],
              }
            } else if (t.name === 'Deposit') {
              return {
                name: t.name,
                id: t.id,
                date: formated,
                status: t.status,
                network: t.network,
                address: t.address,
                amount:
                  t.amount +
                  ' ' +
                  (t.network ||
                    {
                      Ethereum: 'ETH',
                      Bitcoin: 'BTC',
                      Litecoin: 'LTC',
                      'Usd coin': 'USDC',
                    }[t.currency]),
              }
            } else if (t.name === 'Withdrawal') {
              return {
                name: t.name,
                id: t.id,
                date: formated,
                address: t.address,
                status: t.status,
                network: t.network,
                type: t.type,
                amount:
                  t.amount +
                  ' ' +
                  (t.network ||
                    {
                      Ethereum: 'ETH',
                      Bitcoin: 'BTC',
                      Litecoin: 'LTC',
                      'Usd coin': 'USDC',
                    }[t.currency]),
              }
            }
          })
        : []

      return result
    },
    isDesktop() {
      return this.$store.getters.isDesktop
    },
    transfers() {
      return this.selectedType == 'Transfer'
        ? this.transactions.filter(t => t.name == 'Transfer')
        : []
    },
    withdrawals() {
      return this.selectedType == 'Withdrawal'
        ? this.transactions.filter(t => t.name == 'Withdrawal')
        : []
    },
    deposits() {
      return this.selectedType == 'Deposit'
        ? this.transactions.filter(t => t.name == 'Deposit')
        : []
    },
    noSelectedTransactions() {
      return (
        this.transactions.filter(t => t.name == this.selectedType).length == 0
      )
    },
  },
  methods: {
    refreshTransactions() {
      this.updating = true
      Transactions.getList(this.user.id)
        .then(({ data: { transactions } }) => {
          this.$store.commit('UPDATE_USER', { transactions })
          this.updating = false
        })
        .catch(() => {
          this.updating = false
        })
    },
    changeStatusType() {
      this.currentStatusType += this.currentStatusType > 1 ? -2 : 1
    },
    getStatusTypeIcon(status) {
      return {
        error: 'times-circle',
        processing: 'clock',
        success: 'check-circle',
      }[status]
    },
    editStatus(withdrawal) {
      this.editingTransaction = withdrawal
      this.newWithdrawalStatus = withdrawal.status
    },
    saveStatus() {
      let withdrawal = this.editingTransaction
      withdrawal.type = this.currentStatusType
      withdrawal.status = this.newWithdrawalStatus
      this.editingTransaction = null

      Transactions.changeWithdrawalStatus(
        withdrawal.id,
        this.newWithdrawalStatus,
        this.currentStatusType
      )
        .then(({ data }) => {
          this.updating = false

          this.showToast(data.message, {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.updating = false

          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else {
            console.log(err)
          }
        })
    },
    copyWithdrawalAddress(address) {
      this.$copyText(address)

      this.showToast('The address has copied to the clipboard', {
        icon: 'fa-check',
        position: 'bottom-center',
        duration: 4000,
        type: 'success',
      })
    },
    verifyWithdrawal(withdrawal) {
      this.updating = true
      Transactions.verifyWithdrawal(withdrawal.id, this.user.id)
        .then(({ data }) => {
          this.updating = false
          withdrawal.status = 'completed'
          let currency =
            withdrawal.currency || this.netToCurrency(withdrawal.network)
          this.user.wallets[
            currency.toLowerCase()
          ].balance -= +withdrawal.amount.split(' ')[0]

          this.showToast(data.message, {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.updating = false
          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else {
            console.log(err)
          }
        })
    },
    updateCollapseHeight() {
      this.collapseUpdateToggle = !this.collapseUpdateToggle
    },
    netToCurrency: net =>
      ({
        BTC: 'bitcoin',
        LTC: 'litecoin',
        ETH: 'ethereum',
        USDC: 'usd coin',
      }[net.toUpperCase()]),
    deleteTransaction(id) {
      this.updating = true

      var pending = {
        Transfer: () => Transactions.remove(id, this.user.id),
        Deposit: () => Transactions.removeDeposit(id, this.user.id),
        Withdrawal: () => Transactions.removeWithdrawal(id, this.user.id),
      }[this.selectedType]()

      pending
        .then(({ data: transaction }) => {
          this.updating = false
          this.$root.$emit('remove-transaction', transaction)
          this.showToast(`The ${this.selectedType.toLowerCase()} deleted`, {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.updating = false

          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else if (err.request) {
            console.log(err.request)
          } else {
            console.log(err)
          }
        })
    },
    netToCurrency: net =>
      ({
        BTC: 'bitcoin',
        LTC: 'litecoin',
        ETH: 'ethereum',
        USDC: 'usd coin',
      }[net]),
    createTransaction() {
      this.updating = true

      var pending = {
        Transfer: () =>
          Transactions.create({
            user: this.user.id,
            direction: this.selectedDirection,
            action: 'Transfer',
            amount: +this.coinAmount,
            net: this.selectedCurrency,
            date: !this.currentDateToggle ? this.customDatetime : +new Date(),
          }),
        Deposit: () =>
          Transactions.create({
            user: this.user.id,
            action: 'Deposit',
            amount: +this.coinAmount,
            net: this.selectedCurrency,
            date: !this.currentDateToggle ? this.customDatetime : +new Date(),
          }),
        Withdrawal: () =>
          Transactions.create({
            user: this.user.id,
            action: 'Withdraw',
            amount: +this.coinAmount,
            net: this.selectedCurrency,
            address: this.addressToWithdraw,
            date: !this.currentDateToggle ? this.customDatetime : +new Date(),
          }),
      }[this.selectedType]()

      pending
        .then(({ data }) => {
          this.updating = false

          if (data.transfer && data.deposit) {
            this.$root.$emit('add-transaction', data.transfer)
            this.$root.$emit('add-transaction', data.deposit)

            this.showToast('Verified deposit successfully created!', {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 4000,
              type: 'success',
            })
          } else {
            this.$root.$emit('add-transaction', data)

            this.showToast(`${data.name} successfully created!`, {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 4000,
              type: 'success',
            })
          }
        })
        .catch(err => {
          this.updating = false

          if (err.response) {
            this.showToast(err.response.data.message, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          } else if (err.request) {
            console.log(err.request)
          } else {
            console.log(err)
          }
        })
    },
    getStatusColor(status, css) {
      let color = {
        processing: css ? '#EAD94C' : 'warning',
        pending: css ? '#EAD94C' : 'warning',
        'await approval': css ? '#EAD94C' : 'warning',
        failed: css ? '#F4442E' : 'danger',
        completed: css ? '#00ff00' : 'success',
        success: css ? '#00ff00' : 'success',
      }[status]

      return color || '#aaa'
    },
  },
  mounted() {},
}
</script>

<style lang="scss" scoped>
.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}

.transaction_date {
  margin-bottom: 20px;
}

.tab {
  &__create {
    justify-items: center;
  }

  &__transactions-list-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0.5rem;
    margin-top: 2rem;

    h2 {
      width: 100%;
      text-align: left;
    }
  }

  &__status-editing {
    margin-top: 1rem;
  }

  &__transactions {
    display: flex;
    flex-direction: column;
    width: calc(100% + 40px);
    padding: 0 20px;
    margin: 0 -20px;

    &__item {
      display: grid;
      width: 100%;

      div {
        display: grid;
        align-self: center;
        justify-self: center;

        input {
          text-align: center;
        }
      }

      .status_color {
        width: 6px;
        height: 6px;
        border-radius: 100%;
      }

      &.editing {
        height: 40px;
      }

      &.Withdraw {
        grid-template-columns: 1fr 1fr 0.1fr 0.1fr 0.1fr 0.1fr;

        &.editing {
          grid-template-columns: 1fr 1fr 0.3fr 0.1fr 0.1fr;
        }
      }

      &.Transfer {
        grid-template-columns: 1.5fr 1fr 1fr 1fr 0.1fr;
      }

      &.Deposit {
        grid-template-columns: 1fr 1fr 0.1fr;
      }

      &.desktop {
        font-size: 1rem;
        border-left: 0;
        padding-left: 0;

        &.Withdraw {
          grid-template-columns: 1fr 1fr 1fr 0.1fr 0.1fr 0.1fr 0.1fr;

          &.editing {
            grid-template-columns: 1fr 1fr 1fr 1fr 0.1fr;
          }
        }

        &.Transfer {
          grid-template-columns: 1.5fr 1fr 1fr 1fr 1fr 0.1fr;
        }

        &.Deposit {
          grid-template-columns: 1fr 1fr 1fr 0.1fr;
        }
      }
    }

    &__item:nth-child(odd) {
      background-color: #4d4d4d11;
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: start;
    margin-top: 1rem;
    height: 2rem;
  }
}
</style>
