<template>
  <div
    class="transaction-item"
    :style="{
      'grid-template-columns': isDesktop
        ? '1fr 0.5fr 0.5fr 0.5fr 0.8fr 0.5fr'
        : '1fr 1fr 1fr 1fr 0.5fr',
    }"
  >
    <transaction-prop
      align="start"
      style="opacity: .7"
      :description="'out=>' + value.date"
      v-if="isDesktop"
    />
    <transaction-prop :description="'out=>' + value.type" />
    <transaction-prop :description="'out=>' + value.fake" />
    <transaction-prop :description="'out=>' + value.amount" />
    <transaction-prop :description="'sts=>' + value.status" />
    <transaction-prop
      align="end"
      flat
      color="danger"
      description="btn(trash)=>_"
      @click="$emit('delete', value.id)"
    />
  </div>
</template>

<script>
import TransactionProp from './TransactionProp.vue'

export default {
  components: {
    TransactionProp,
  },
  props: {
    value: {
      type: Object,
    },
  },
  data() {
    return {}
  },
  computed: {
    isDesktop() {
      return this.$store.getters.isDesktop
    },
  },
  created() {},
}
</script>

<style lang="scss" scoped>
.transaction-item {
  width: calc(100% - 1rem);
  margin: 0.1rem 0.5rem;
  padding: 0 0.8rem;

  display: grid;
  grid-template-rows: 1fr;

  background-color: #00000033;
  border-top: 2px #ffffff22 solid;
  justify-content: space-between;
}
</style>
