<template>
  <div
    class="transaction-prop"
    :style="{
      justifySelf: align,
    }"
  >
    <va-button
      class="justify--end button"
      small
      @click="$emit('click')"
      :color="color"
      :outline="outline"
      :flat="flat"
      v-if="type == TYPES.BUTTON"
    >
      <va-icon :name="'fa fa-' + icon" />
      <span :class="{ ms: value }">
        {{ value }}
      </span>
    </va-button>
    <div v-else-if="type == TYPES.OUTPUT" class="output">{{ value }}</div>
    <va-badge
      :color="getStatusColor(value, stype)"
      outline
      v-else-if="type == TYPES.STATUS"
      type="text"
    >
      {{ value }}
    </va-badge>
  </div>
</template>

<script>
const TYPES = {
  BUTTON: 'btn',
  OUTPUT: 'out',
  STATUS: 'sts',
}

const descriptionRegex = new RegExp(
  `(${TYPES.BUTTON}(\\([^)]+\\))?|${TYPES.OUTPUT}|${TYPES.STATUS})=>(.+)?`
)

export default {
  props: {
    description: {
      type: String,
      default: 'out:text',
      require: true,
      validator(s) {
        return s.match(descriptionRegex)
      },
    },
    outline: {
      type: Boolean,
    },
    flat: {
      type: Boolean,
    },
    color: {
      type: String,
    },
    align: {
      type: String,
      default: 'center',
    },
    stype: {
      type: Number,
    },
  },
  data: () => ({
    type: null,
    value: null,
    icon: null,
    TYPES,
  }),
  watch: {
    description: {
      handler() {
        let { type, icon, value } = this.parseDescripton()

        this.type = type
        this.value = value === '_' ? '' : value
        this.icon = icon
      },
      immediate: true,
    },
  },
  methods: {
    parseDescripton() {
      let result = this.description.split(/[()=>]/).filter(s => s)

      if (result.length == 3) {
        return { type: result[0], icon: result[1], value: result[2] }
      } else {
        return { type: result[0], icon: null, value: result[1] }
      }
    },
    getStatusColor(status, type) {
      let statuses = {
        processing: 'warning',
        pending: 'warning',
        'await approval': 'warning',
        failed: 'danger',
        completed: 'success',
        success: 'success',
      }

      let color = statuses[status]

      if (typeof type == 'number') {
        return ['success', 'warning', 'danger'][type]
      }

      return color || '#aaa'
    },
  },
  created() {},
}
</script>

<style lang="scss" scoped>
.transaction-prop {
  display: flex;
  align-self: center;
  justify-self: center;

  .output {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    width: 100%;
  }

  input {
    text-align: center;
  }

  .button {
    padding: 0.3rem 0.5rem;
  }
}

.ms {
  margin: 0 0.1rem;
}
</style>
