import axios from 'axios'
import store from '../store'

const base = store.state.app.config.base

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const remove = (id, user) =>
  axios.post(base + '/api/admin/transaction/delete', { id, user })
const removeDeposit = (id, user) =>
  axios.post(base + '/api/admin/deposit/delete', { id, user })
const removeWithdrawal = (id, user) =>
  axios.post(
    base + '/api/admin/withdrawal/delete',
    { id, user },
    { cancelToken }
  )
const verifyWithdrawal = (id, user) =>
  axios.get(base + `/api/admin/user/${user}/withdrawal/${id}/verify`, {
    cancelToken,
  })
const changeWithdrawalStatus = (id, value, type) =>
  axios.post(base + `/api/admin/withdrawal/status`, { status: value, id, type }, {
    cancelToken,
  })
const changeDepositStatus = (id, value) =>
  axios.post(base + `/api/admin/deposit/status`, { status: value, id }, {
    cancelToken,
  })
const create = body =>
  axios.post(base + '/api/admin/transaction/create', body, {
    cancelToken,
    headers: {
      Authorization: store.state.auth.token
    },
  })
const getDepositsList = () =>
  axios.get(base + '/api/admin/deposits', {
    cancelToken,
    headers: {
      Authorization: store.state.auth.token
    },
  })
const getTransfersList = () =>
  axios.get(base + '/api/admin/transfers', {
    cancelToken,
    headers: {
      Authorization: store.state.auth.token
    },
  })

const getList = user =>
  axios.post(base + '/api/admin/transactions', { user }, {
    cancelToken,
    headers: {
      Authorization: store.state.auth.token
    },
  })

export {
  remove,
  create,
  removeDeposit,
  getDepositsList,
  getTransfersList,
  verifyWithdrawal,
  removeWithdrawal,
  changeDepositStatus,
  changeWithdrawalStatus,
  abort,
  getList
}
